import React, { Component } from "react";
import { connect } from "react-redux";
import axios from "axios";

import API from "../../../assets/api.json";
import { AddQuestionToMockTest } from "../../../store/actions/mocktest_actions";

export class AddQuestionModal extends Component {
  state = {
    text: null,
    image: null,
    options: [],
    rightOption: null,
    answer: [],
    imageUploadAlerts: [],
    solution: null,
    solutionImages: [],
  };
  addOption = () => {
    let { options } = this.state;
    let newOptions = [...options];
    let newOptionId = newOptions.length + 1;
    newOptions.push({
      id: newOptionId,
      text: "",
      image: "",
    });
    this.setState({ options: newOptions });
  };
  handleQuestionInputChange = (e) => {
    this.setState({ [e.target.name]: e.target.value });
  };
  handleQuestionImageUpload = (e) => {
    let imageFile = e.target.files[0];
    let optionId = 0;
    let alert = {
      msg: "Uploading Image. Please Don't Reload this page or close",
      optionId: 0,
      display: "block",
      type: "danger",
    };
    let newAlert = this.state.imageUploadAlerts;
    newAlert = newAlert.filter((alert) => alert.optionId !== optionId);
    newAlert.push(alert);
    this.setState({ imageUploadAlerts: newAlert });

    console.log(imageFile);
    let data = new FormData();
    data.append("image", imageFile);
    const token = localStorage.getItem("token");
    let req_options = {
      url: `${API.proxy}/mocktest/api/upload`,
      method: "POST",
      header: {
        "content-type":
          "multipart/form-data; boundary=---011000010111000001101001",
        authorization: `Bearer ${token}`,
      },
      data: data,
    };
    axios(req_options)
      .then((res) => {
        console.log(res.data);
        let { file_url } = res.data;
        this.setState({ image: file_url });
        let alert = {
          msg: "Image Uploaded",
          optionId: optionId,
          display: "block",
          type: "success",
        };
        let newAlert = this.state.imageUploadAlerts;
        newAlert = newAlert.filter((alert) => alert.optionId !== optionId);
        newAlert.push(alert);
        this.setState({ imageUploadAlerts: newAlert });
        // console.log(optionId, filteredOption);
      })
      .catch((err) => {
        console.log(JSON.stringify(err));
        console.log(err.response);
      });
  };
  deleteOption = (optionId) => {
    let { options } = this.state;
    let filteredOption = options.filter((op) => op.id !== optionId);
    this.setState({ options: filteredOption });
    // console.log(option);
  };
  handleOptionTextChange = (e, optionId) => {
    let { options } = this.state;
    let updatedOptions = options.filter((op) => op.id !== optionId);
    let filteredOption = options.filter((op) => op.id === optionId)[0];

    filteredOption["text"] = e.target.value;
    updatedOptions.push(filteredOption);
    this.setState({ options: updatedOptions });
    console.log(optionId, filteredOption);
  };

  handleOptionImageChange = (e, optionId) => {
    let imageFile = e.target.files[0];

    let alert = {
      msg: "Uploading Image. Please Don't Reload this page or close",
      optionId: optionId,
      display: "block",
      type: "danger",
    };
    let newAlert = this.state.imageUploadAlerts;
    newAlert = newAlert.filter((alert) => alert.optionId !== optionId);
    newAlert.push(alert);
    this.setState({ imageUploadAlerts: newAlert });

    console.log(imageFile);
    let data = new FormData();
    data.append("image", imageFile);
    const token = localStorage.getItem("token");
    let req_options = {
      url: `${API.proxy}/mocktest/api/upload`,
      method: "POST",
      header: {
        "content-type":
          "multipart/form-data; boundary=---011000010111000001101001",
        authorization: `Bearer ${token}`,
      },
      data: data,
    };
    axios(req_options)
      .then((res) => {
        console.log(res.data);
        let { file_url } = res.data;
        let { options } = this.state;
        let updatedOptions = options.filter((op) => op.id !== optionId);
        let filteredOption = options.filter((op) => op.id === optionId)[0];

        filteredOption["image"] = file_url;
        updatedOptions.push(filteredOption);
        this.setState({ options: updatedOptions });

        let alert = {
          msg: "Image Uploaded",
          optionId: optionId,
          display: "block",
          type: "success",
        };
        let newAlert = this.state.imageUploadAlerts;
        newAlert = newAlert.filter((alert) => alert.optionId !== optionId);
        newAlert.push(alert);
        this.setState({ imageUploadAlerts: newAlert });

        console.log(optionId, filteredOption);
      })
      .catch((err) => {
        console.log(err);
        console.log(err.response);
      });
  };
  handleAddQuestionFormSubmit = (e) => {
    e.preventDefault();
    this.props.AddQuestionToMockTest(this.state);
    // console.log(this.state);
  };

  rightAnswerSelection = (e) => {
    // console.log(e.target);
    let options = e.target.options;
    let answer = [];
    for (let i = 0; i < options.length; i++) {
      const option = options[i];
      if (option.selected) {
        answer.push(option.value);
      }
    }
    this.setState({ answer: answer });
  };
  solutionImageComponent = () => {
    return (
      <div
        class="alert alert-info alert-dismissible fade show m-0"
        role="alert"
      >
        <input
          type="file"
          name="image"
          onChange={this.handleQuestionImageUpload}
          className="form-control col-10"
          placeholder="Question Image"
          aria-label="Question Image"
          aria-describedby="button-addon2"
        />
        <div className="">
          <span>
            Uploading <i class="fa fa-circle-o-notch" aria-hidden="true"></i>
          </span>
        </div>
        <button
          type="button"
          class="close"
          data-dismiss="alert"
          aria-label="Close"
        >
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
    );
  };
  render() {
    let { options, question, imageUploadAlerts } = this.state;
    return (
      <div
        className="modal fade"
        id="QuestionModal"
        tabIndex="-1"
        aria-labelledby="QuestionModalLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-lg">
          <div className="modal-content">
            <div className="modal-header">
              <p className="modal-title" id="QuestionModalLabel">
                Question
              </p>
              <button
                type="button"
                className="close"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-body">
              <formdiv
                className="add-question-form"
                onSubmit={this.handleAddQuestionFormSubmit}
              >
                <div className="form-group">
                  <label htmlFor="add-q-modal-question">Question</label>
                  <textarea
                    className="form-control"
                    name="text"
                    id="add-q-modal-question"
                    onChange={this.handleQuestionInputChange}
                  >
                    {question}
                  </textarea>
                </div>
                <div className="input-group">
                  {imageUploadAlerts
                    .filter((alert) => alert.optionId === 0)
                    .map((alrt) => (
                      <div
                        class={`alert alert-${alrt.type} alert-dismissible fade show d-${alrt.display}`}
                        role="alert"
                      >
                        {alrt.msg}
                        <button
                          type="button"
                          class="close"
                          data-dismiss="alert"
                          aria-label="Close"
                        >
                          <span aria-hidden="true">&times;</span>
                        </button>
                      </div>
                    ))}
                  <input
                    type="file"
                    name="image"
                    onChange={this.handleQuestionImageUpload}
                    className="form-control"
                    placeholder="Question Image"
                    aria-label="Question Image"
                    aria-describedby="button-addon2"
                  />
                  <div className="input-group-append">
                    <button
                      className="btn btn-outline-success"
                      type="button"
                      id="button-addon2"
                    >
                      <i className="fa fa-upload"></i>
                    </button>
                  </div>
                </div>
                <div className="question-options mt-2">
                  <label>Options</label>

                  {options.map((option) => (
                    <div>
                      {imageUploadAlerts
                        .filter((alert) => alert.optionId === option.id)
                        .map((alrt) => (
                          <div
                            class={`alert alert-${alrt.type} alert-dismissible fade show d-${alrt.display}`}
                            role="alert"
                          >
                            {alrt.msg}
                            <button
                              type="button"
                              class="close"
                              data-dismiss="alert"
                              aria-label="Close"
                            >
                              <span aria-hidden="true">&times;</span>
                            </button>
                          </div>
                        ))}
                      <div class="alert alert-primary" role="alert">
                        <div className="input-group" key={option.id}>
                          <input
                            type="text"
                            className="form-control"
                            placeholder="Option Text"
                            aria-label="New Option"
                            aria-describedby="button-addon4"
                            value={option.text}
                            onChange={(e, optionId) =>
                              this.handleOptionTextChange(e, option.id)
                            }
                          />
                          <input
                            type="file"
                            className="form-control"
                            onChange={(e, optionId) =>
                              this.handleOptionImageChange(e, option.id)
                            }
                          />
                          <div
                            className="input-group-append"
                            id="button-addon4"
                          >
                            <button
                              className="btn btn-outline-danger"
                              type="button"
                              onClick={() => this.deleteOption(option.id)}
                            >
                              <i className="fa fa-trash"></i>
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  ))}
                  <button
                    type="button"
                    className="btn btn-success add-option container"
                    onClick={this.addOption}
                  >
                    Add Option <i className="fa fa-plus"></i>
                  </button>
                </div>

                <div class="form-group">
                  <label for="right-answer">Select Right answer</label>
                  <select
                    multiple
                    class="form-control"
                    id="right-answer"
                    onChange={this.rightAnswerSelection}
                  >
                    {options.map((option) => (
                      <option
                        value={option.id}
                      >{`(${option.id}.)${option.text} [${option.image}]`}</option>
                    ))}
                  </select>
                </div>

                <div className="form-group">
                  <label htmlFor="add-q-modal-solution">Solution</label>
                  <textarea
                    className="form-control"
                    name="text"
                    id="add-q-modal-solution"
                    onChange={true}
                  ></textarea>
                </div>
                <div className="solution-image-conatine">
                  <div className="solution-images">
                    {this.solutionImageComponent()}
                    {this.solutionImageComponent()}
                    {this.solutionImageComponent()}
                  </div>

                  <button type="button" class="btn btn-primary btn-lg mt-2">
                    <i className="fa-plus fa"></i> Add Solution Image
                  </button>
                </div>
                <div className="form-group mt-3">
                  <button
                    type="button"
                    onClick={this.handleAddQuestionFormSubmit}
                    className="btn btn-primary float-right"
                    data-dismiss="modal"
                  >
                    Submit
                  </button>
                </div>
              </formdiv>
            </div>
            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-secondary"
                data-dismiss="modal"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

function mapDispatchToProps(dispatch) {
  return {
    AddQuestionToMockTest: (e) => dispatch(AddQuestionToMockTest(e)),
  };
}

export default connect(null, mapDispatchToProps)(AddQuestionModal);
