import React, { Component } from "react";
import axios from "axios";
// import $ from ''
import Skeleton from "react-loading-skeleton";

import API from "../../assets/api.json";
import SideNav from "./SideNav";

export class ProfileUpdate extends Component {
  state = {
    email: null,
    phoneNo: null,
    studentboard: [],
    studentclass: [],
    subject: [],
    image: null,
  };
  componentDidMount = () => {
    const token = localStorage.getItem("token");
    const { userId } = JSON.parse(localStorage.getItem("userData"));
    const options = {
      method: "GET",
      url: `${API.proxy}/profile`,
      params: { userId: userId },
      headers: {
        "content-type": "application/json",
        authorization: `Bearer ${token}`,
      },
    };

    axios
      .request(options)
      .then((response) => {
        let { email, phoneNo, image } = response.data.Result.user;
        let { studentboard, studentclass, subject } = response.data.Result;
        this.setState({
          email,
          phoneNo,
          studentboard,
          studentclass,
          subject,
          image,
        });
        console.log(response.data.Result);
      })
      .catch((error) => {
        console.error(error);
      });
  };

  teacherImage = () => {
    if (this.state.image === null) {
      return <Skeleton circle={true} height={100} width={100} />;
    } else {
      return (
        <img
          id="profile_pic"
          src={this.state.image}
          alt="..."
          className="rounded-circle"
          width={100}
          height={100}
        />
      );
    }
  };
  render() {
    let teacher = this.state;

    return (
      <main className="pb-5 main col-10 container">
        <div>
          <div className="row">
            <div className="col-md-8">
              <div className="container-lg">
                <div className="shadow-lg py-5 px-3 mb-5 bg-white rounded">
                  <div className="container my-form">
                    <legend className="text-center">Profile Update</legend>
                    <form>
                      <div className="text-center">
                        <div className="mb-2">{this.teacherImage()}</div>
                        <div className="file-field">
                          <div className="btn btn-sm btn-primary px-2 py-1 btn-rounded">
                            Update Image
                            <input
                              type="file"
                              name="image"
                              accept="image/*"
                              onChange="readURL(this);"
                            />
                          </div>
                        </div>
                      </div>
                      <div className="form-group">
                        <label htmlFor="exampleFormControlInput1">
                          Email address
                        </label>
                        <input
                          disabled
                          type="email"
                          className="form-control"
                          id="exampleFormControlInput1"
                          placeholder={teacher.email}
                          defaultValue={teacher.email}
                        />
                      </div>
                      <div className="form-group">
                        <label htmlFor="exampleFormControlInput1">
                          Phone number
                        </label>
                        <input
                          disabled
                          type="text"
                          className="form-control"
                          id="exampleFormControlInput1"
                          defaultValue={teacher.phoneNo}
                        />
                      </div>
                      <div className="form-group">
                        <label htmlFor="exampleFormControlSelect1">
                          Medium
                        </label>
                        <select
                          className="form-control"
                          id="exampleFormControlSelect1"
                        >
                          <option>Hindi</option>
                          <option>English</option>
                        </select>
                      </div>
                      <div className="form-group">
                        <label htmlFor="exampleFormControlSelect21">
                          Class
                        </label>
                        <select
                          multiple
                          className="form-control"
                          id="exampleFormControlSelect21"
                        >
                          <option value="8th" className="class-input">
                            8th
                          </option>
                          <option value="9th" className="class-input">
                            9th
                          </option>
                          <option value="10th" className="class-input">
                            10th
                          </option>
                        </select>
                        <small className="text-muted">
                          Hold down “Control”, or “Command” on a Mac, to select
                          more than one.
                        </small>
                      </div>
                      <div className="form-group">
                        <label htmlFor="exampleFormControlSelect22">
                          Board
                        </label>
                        <select
                          multiple
                          className="form-control"
                          id="exampleFormControlSelect22"
                        >
                          <option selected>CBSE</option>
                          <option>Other Board</option>
                        </select>
                        <small className="text-muted">
                          Hold down “Control”, or “Command” on a Mac, to select
                          more than one.
                        </small>
                      </div>
                      <div className="form-group">
                        <label htmlFor="exampleFormControlTextarea1">
                          Your Description
                        </label>
                        <textarea
                          className="form-control"
                          id="exampleFormControlTextarea1"
                          placeholder="This is my old description."
                          rows={2}
                          defaultValue={""}
                        />
                      </div>
                      <div className="text-center">
                        <button
                          type="submit"
                          className="btn btn-sm btn-primary mt-3"
                        >
                          submit
                        </button>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
            <SideNav />
          </div>
        </div>
      </main>
    );
  }
}

export default ProfileUpdate;
