import * as ACTION_TYPE from "../actions/action_types";

let initalState = {
  isLoggedIn: false,
  userData: null,
  loginFormAlert: {},
  liveStreams: [
    {
      created_on: "4 hours ago",
      date: "26/09/2020",
      discription: "https://www.youtube.com/watch?v=NvCSr7qzAAM",
      id: 2,
      teacher: "site teacher",
      thumbnail: "https://mirrorscore-test.s3.amazonaws.com/media/null",
      time: "04:25 AM",
      title: "testing 123",
      url: "https://www.youtube.com/watch?v=NvCSr7qzAAM",
    },
  ],
  dataLoaded: false,
  updateLiveStream: {},
  updateLiveStreamAlert: {},
};

const dashboardReducer = (state = initalState, action) => {
  let payload = action.payload;
  switch (action.type) {
    case ACTION_TYPE.LoggedInUser:
      // console.log(payload.status);
      return {
        ...state,
        isLoggedIn: payload.status,
        userData: payload.userData,
        loginFormAlert: {
          alertType: payload.alertType,
          alertMsg: payload.alertMsg,
          alertDisplay: payload.alertDisplay,
        },
      };
    case ACTION_TYPE.GetLiveStreams:
      // console.log(payload.liveStreams);
      return {
        ...state,
        dataLoaded: payload.dataLoaded,
        liveStreams: payload.liveStreams,
      };
    case ACTION_TYPE.FetchLiveStream:
      return {
        ...state,
        updateLiveStream: payload.liveStream,
        updateLiveStreamAlert: {
          alertType: payload.alertType,
          alertMsg: payload.alertMsg,
          alertDisplay: payload.alertDisplay,
        },
      };
    case ACTION_TYPE.PutLiveStream:
      return {
        ...state,
        updateLiveStreamAlert: {
          alertType: payload.alertType,
          alertMsg: payload.alertMsg,
          alertDisplay: payload.alertDisplay,
        },
      };
    default:
      return {
        ...state,
      };
  }
};

export default dashboardReducer;
