import * as ACTION_TYPE from "./action_types";
import axios from "axios";
import $ from "jquery";

import API from "../../assets/api.json";

export const GetLiveStreams = () => {
  return (dispatch) => {
    // console.log("Lives");
    const token = localStorage.getItem("token");
    const { userId } = JSON.parse(localStorage.getItem("userData"));
    // console.log(token, userId);
    const options = {
      method: "GET",
      url: `${API.proxy}/liveStream/`,
      params: { userId: userId },
      headers: {
        "content-type": "application/json",
        authorization: `Bearer ${token}`,
      },
    };
    // console.log("GetLives");
    axios(options)
      .then((res) => {
        console.log(res.data.Result.data);
        dispatch({
          type: ACTION_TYPE.GetLiveStreams,
          payload: {
            dataLoaded: true,
            liveStreams: res.data.Result.data,
          },
        });
      })
      .catch((error) => {
        console.error(error);
      });
  };
};

export const SetUserLoggedIn = (userData) => {
  return {
    type: ACTION_TYPE.LoggedInUser,
    payload: {
      dataLoaded: true,
      userData: userData,
    },
  };
};

export const FetchLiveStream = (streamId) => {
  const token = localStorage.getItem("token");
  const { userId } = JSON.parse(localStorage.getItem("userData"));
  const options = {
    method: "GET",
    url: `${API.proxy}/liveStream/`,
    params: { userId: userId, id: streamId },
    headers: {
      authorization: `Bearer ${token}`,
      "content-type": "application/json",
    },
    data: {},
  };
  return (dispatch) => {
    // console.log(streamId + " Fetch Live Stream");

    axios(options)
      .then((res) => {
        // console.log(res.data);
        const stream = res.data.Result.data[0];
        console.log(stream);
        let { title, url, date, time, discription } = stream;
        let formatedTime = time.replace(" AM", "");
        // console.log(formatedTime);
        time = formatedTime;
        dispatch({
          type: ACTION_TYPE.FetchLiveStream,
          payload: {
            liveStream: { title, url, discription, date, time },
            alertType: "light",
            alertMsg: null,
            alertDisplay: "none",
          },
        });
      })
      .catch((error) => {
        try {
          let { status } = error.response;
          if (status === 404) {
            $(".yt-stream-update-form").hide();
            let error_msg = `${error.response.data.Comments}\nRedirecting....`;

            dispatch({
              type: ACTION_TYPE.FetchLiveStream,
              payload: {
                liveStream: {},
                alertType: "danger",
                alertMsg: error_msg,
                alertDisplay: "block",
              },
            });
            setTimeout(() => {
              window.location = "/dashboard/yt-lives";
            }, 4000);
          }
        } catch (error) {}
      });
  };
};

export const PutLiveStream = (streamId, data) => {
  return (dispatch) => {
    console.log("update live stream", data);
    const token = localStorage.getItem("token");
    const { userId } = JSON.parse(localStorage.getItem("userData"));
    const { url, date, time, discription, thumbnail } = data;
    let formdata = new FormData();
    formdata.append("title", "testing 123");
    formdata.append("discription", discription);
    formdata.append("url", url);
    formdata.append("date", date);
    formdata.append("time", time);
    formdata.append("thumbnail", thumbnail);
    formdata.append("id", streamId);

    const options = {
      method: "PUT",
      url: `${API.proxy}/liveStream/`,
      params: { userId: userId },
      headers: {
        "content-type":
          "multipart/form-data; boundary=---011000010111000001101001",
        authorization: `Bearer ${token}`,
      },
      data: formdata,
    };

    axios(options)
      .then((res) => {
        const { data } = res;
        console.log(data);
        window.location = "";
      })
      .catch((error) => {
        try {
          let error_msg = error.response.data.Comments;
          console.log(error_msg);
          dispatch({
            type: ACTION_TYPE.PutLiveStream,
            payload: {
              alertType: "danger",
              alertMsg: error_msg,
              alertDisplay: "block",
            },
          });
        } catch (error) {
          console.log(error);
        }
      });
  };
};
