import React, { Component } from "react";
import { connect } from "react-redux";
// import axios from "axios";
import { Link } from "react-router-dom";

import { GetLiveStreams } from "../../store/actions/dashboard_actions";
import SideNav from "./SideNav";
import YouTubeLive from "./YouTubeLive";
import YouTubeLiveLoading from "./YouTubeLiveLoading";
// import YouTubeLivesPagination from "./YouTubeLivesPagination";
// import API from "../../assets/api.json";

export class YouTubeLives extends Component {
  state = {
    dataLoaded: false,
    liveStreams: [],
  };
  componentDidMount = () => {
    this.props.GetLiveStreams();
  };

  YoutubeLives = () => {
    // console.log(this.props.dataLoaded);
    if (!this.props.dataLoaded) {
      let DummyData = [{ id: 1 }, { id: 2 }, { id: 3 }, { id: 4 }];
      return DummyData.map((data) => (
        <YouTubeLiveLoading key={data.id} livestream={DummyData} />
      ));
    } else {
      return this.props.liveStreams.map((data) => (
        <YouTubeLive key={data.id} livestream={data} />
      ));
    }
  };

  YoutubeLivesPagination = () => {
    if (!this.props.dataLoaded) {
    } else {
      // let livesLen = this.props.liveStreams.length;
      // let livePages = livesLen / 10;
      // console.log(livesLen, livePages);
      return (
        <div className="my-2">
          <nav aria-label="Page navigation example">
            {/* <YouTubeLivesPagination data={lives} /> */}
          </nav>
        </div>
      );
    }
  };

  render() {
    // let { liveStreams } = this.state;
    return (
      <main className="pb-5 main col-lg-10 container">
        <div>
          <div className="row ">
            <div className="col-md-8">
              <div className="container-lg">
                <div className="shadow-lg py-5 px-3 mb-5 bg-white rounded">
                  <div className="container px-3">
                    <div className="btn-group">
                      <button
                        type="button"
                        className="btn btn-secondary dropdown-toggle m-1"
                        data-toggle="dropdown"
                        aria-haspopup="true"
                        aria-expanded="false"
                      >
                        Filter
                      </button>
                      <div className="dropdown-menu">
                        <a className="dropdown-item" href="#/">
                          Live
                        </a>
                        <a className="dropdown-item" href="#/">
                          Incoming
                        </a>
                        <a className="dropdown-item" href="#/">
                          Cancelled
                        </a>
                        <div className="dropdown-divider" />
                        <a className="dropdown-item" href="#/">
                          Completed
                        </a>
                      </div>
                    </div>
                    <Link
                      to="/dashboard/add-stream"
                      className="btn btn-info m-1"
                    >
                      New LiveStream
                    </Link>
                    <div>{this.YoutubeLives()}</div>

                    {this.YoutubeLivesPagination()}
                  </div>
                  <div className="container text-center">
                    <Link
                      to="/dashboard/add-stream"
                      className="btn btn-sm btn-primary mt-3 mr-sm-3"
                    >
                      Add New Stream
                    </Link>
                  </div>
                </div>
              </div>
            </div>

            <SideNav />
          </div>
        </div>
      </main>
    );
  }
}

function mapStateToProps(state) {
  return {
    isLoggedIn: state.dashboardReducer.isLoggedIn,
    dataLoaded: state.dashboardReducer.dataLoaded,
    liveStreams: state.dashboardReducer.liveStreams,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    GetLiveStreams: (e) => dispatch(GetLiveStreams(e)),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(YouTubeLives);
