import React, { Component } from "react";
import SideNav from "./SideNav";
import axios from "axios";

import API from "../../assets/api.json";

export class AddLiveStream extends Component {
  state = {
    title: "",
    url: null,
    thumbnail: null,
    date: null,
    time: null,
    medium: null,
    klass: null,
    description: null,
    alertType: "light",
    alertMsg: "",
    alertDisplay: "none",
  };
  handleInputChange = (e) => {
    this.setState({ [e.target.name]: e.target.value });
    // console.log(e.target.value);
  };
  handleFileSelection = (e) => {
    this.setState({ [e.target.name]: e.target.files[0] });
    // console.log(e.target.files[0]);
  };
  handleFormSubmit = (e) => {
    e.preventDefault();
    let { url, thumbnail, date, time, description } = this.state;

    const token = localStorage.getItem("token");
    const { userId } = JSON.parse(localStorage.getItem("userData"));
    console.log(token, userId);
    const formData = new FormData();
    formData.append("title", "testing 123");
    formData.append("discription", description);
    formData.append("url", url);
    formData.append("date", date);
    formData.append("time", time);
    formData.append("thumbnail", thumbnail);

    const options = {
      method: "POST",
      url: `${API.proxy}/liveStream/`,
      params: { userId: userId },
      headers: {
        "content-type":
          "multipart/form-data; boundary=---011000010111000001101001",
        authorization: `Bearer ${token}`,
      },
      data: formData,
    };

    axios(options)
      .then((res) => {
        const { data } = res;
        console.log(data);
        this.setState({ alertType: "success" });
        this.setState({ alertMsg: data.Comments });
        this.setState({ alertDisplay: "block" });
        window.location = "/dashboard/yt-lives";
      })
      .catch((error) => {
        try {
          let error_msg = error.response.data.Comments;
          this.setState({ alertType: "danger" });
          this.setState({ alertMsg: error_msg });
          this.setState({ alertDisplay: "block" });
        } catch (error) {}
        console.error(error.response.data.Comments);
      });
    // console.log(this.state);
  };
  render() {
    return (
      <main className="pb-5 main col-10 container">
        <div className="row ">
          <div className="col-md-8">
            <div className="container-lg">
              <div className="shadow-lg py-5 px-3 mb-5 bg-white rounded">
                <div className="container my-form">
                  <legend className="text-center">Add New LiveStream</legend>
                  <div
                    className={`alert alert-${this.state.alertType} container col-8 text-center`}
                    role="alert"
                    style={{ display: this.state.alertDisplay }}
                  >
                    {this.state.alertMsg}
                  </div>
                  <form onSubmit={this.handleFormSubmit}>
                    <div className="form-group">
                      <label htmlFor="exampleFormControlInput10">
                        Youtube Link
                      </label>
                      <input
                        onChange={this.handleInputChange}
                        name="url"
                        type="url"
                        className="form-control"
                        id="exampleFormControlInput10"
                        placeholder="https://yoube.be/anbsjdjbsk/"
                      />
                    </div>
                    <div className="form-group">
                      <label htmlFor="exampleFormControlFile1">Thumbnail</label>
                      <input
                        onChange={this.handleFileSelection}
                        name="thumbnail"
                        type="file"
                        className="form-control-file"
                        id="exampleFormControlFile1"
                      />
                    </div>
                    <div className="form-group">
                      <label htmlFor="exampleFormControlInput11">Date</label>
                      <input
                        name="date"
                        onChange={this.handleInputChange}
                        type="date"
                        className="form-control"
                        id="exampleFormControlInput11"
                        required
                      />
                    </div>
                    <div className="form-group">
                      <label htmlFor="exampleFormControlInput12">Time</label>
                      <input
                        name="time"
                        onChange={this.handleInputChange}
                        type="time"
                        className="form-control"
                        id="exampleFormControlInput12"
                        required
                      />
                      <small className="text-muted">Fill like "08:40 am"</small>
                    </div>
                    <div className="form-group">
                      <label htmlFor="exampleFormControlSelect1">Medium</label>
                      <select
                        className="form-control"
                        id="exampleFormControlSelect1"
                        name="medium"
                        onChange={this.handleInputChange}
                      >
                        <option>--select--</option>
                        <option>Hindi</option>
                        <option>English</option>
                      </select>
                    </div>
                    <div className="form-group">
                      <label htmlFor="exampleFormControlSelect11">Class</label>
                      <select
                        className="form-control"
                        id="exampleFormControlSelect11"
                        name="class"
                        onChange={this.handleInputChange}
                      >
                        <option>--select--</option>
                        <option>8th</option>
                        <option>9th</option>
                        <option>10th</option>
                      </select>
                    </div>
                    <div className="form-group">
                      <label htmlFor="exampleFormControlSelect12">
                        Subject
                      </label>
                      <select
                        className="form-control"
                        id="exampleFormControlSelect12"
                        onChange={this.handleInputChange}
                      >
                        <option>--select--</option>
                        <option>Math</option>
                        <option>Science</option>
                      </select>
                    </div>
                    <div className="form-group">
                      <label htmlFor="exampleFormControlTextarea1">
                        Description
                      </label>
                      <textarea
                        onChange={this.handleInputChange}
                        name="description"
                        className="form-control"
                        id="exampleFormControlTextarea1"
                        placeholder="This is my old description."
                        rows={2}
                        defaultValue={""}
                      />
                    </div>
                    <div className="text-center">
                      <button
                        type="submit"
                        className="btn btn-sm btn-primary mt-3"
                      >
                        submit
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
          <SideNav />
          {/* <div className="col-md-4">
              <div className="container-lg side-bar pb-sm-5">
                <div className="shadow-lg py-5 px-3 bg-white rounded">
                  <ul className="my-2">
                    <li>
                      <a href="/#">
                        <i className="ti-youtube text-danger shadow rounded bg-white mr-1" />{" "}
                        youtube dashboard
                      </a>
                    </li>
                  </ul>
                </div>
              </div>
            </div> */}
        </div>
      </main>
    );
  }
}

export default AddLiveStream;
