import React from "react";
import { Route, Switch, useRouteMatch } from "react-router-dom";
import { connect } from "react-redux";

import ProfileView from "../components/Dashboard/ProfileView";
import YouTubeLives from "../components/Dashboard/YouTubeLives";
import AddLiveStream from "../components/Dashboard/AddLiveStream";
import UpdateLiveStream from "../components/Dashboard/UpdateLiveStream";
import ProfileUpdate from "../components/Dashboard/ProfileUpdate";
import MockTest from "../components/Dashboard/MockTest/index";
// import * as ACTIONS from "../store/actions/action_types";

// import Contact from "../components/Contact";

function DashboardView() {
  let { path } = useRouteMatch();
  return (
    <Switch>
      <Route exact path={path}>
        <ProfileView />
      </Route>
      <Route path={`${path}/yt-lives`}>
        <YouTubeLives />
      </Route>
      <Route path={`${path}/add-stream`}>
        <AddLiveStream />
      </Route>
      <Route path={`${path}/update-stream/:streamId`}>
        <UpdateLiveStream />
      </Route>
      <Route path={`${path}/profile-update`}>
        <ProfileUpdate />
      </Route>
      <Route path={`${path}/mocktest`}>
        <MockTest />
      </Route>
    </Switch>
  );
}

export class Dashboard extends React.Component {
  state = {
    isLoggedIn: false,
    userData: [],
  };
  componentDidMount = () => {
    let isLoggedIn = JSON.parse(localStorage.getItem("isLoggedIn"));
    if (!isLoggedIn) {
      console.log(isLoggedIn, "user not logged In");
      window.location = "/login";
    }
  };
  render() {
    return (
      <main className="main">
        <div>
          <div className="row no-gutters">
            <DashboardView />
          </div>
        </div>
      </main>
    );
  }
}

function mapStateToProps(state) {
  return {
    isLoggedIn: state.dashboardReducer.isLoggedIn,
  };
}

export default connect(mapStateToProps, null)(Dashboard);
