import React, { Component } from "react";
import Testimonial from "./Testimonial";
import { connect } from "react-redux";

export class Testimonials extends Component {
  state = {
    Testimonials: this.props.getTestimonials,
  };
  componentDidMount = () => {
    console.log(this.props.getTestimonials);
  };
  render() {
    // const Testimonials = this.props.getTestimonials;
    const { Testimonials } = this.state;

    return (
      <div className="section">
        <div className="testimonialmain">
          <div className="container">
            <div className="row">
              <div className="col-md-12">
                <div id="testimonial-slider" className="owl-carousel">
                  {Testimonials.map((testimonial, index) => (
                    <Testimonial data={testimonial} key={index} />
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    getTestimonials: state.landingReducer.Testimonials,
  };
}

export default connect(mapStateToProps, null)(Testimonials);
