import React, { Component } from "react";
// import axios from "axios";
import { connect } from "react-redux";
// import { useHistory } from "react-router-dom";
import * as ACTIONS from "../store/actions/actions";

export class Login extends Component {
  state = {
    email: "teacher@site.com",
    password: "password123",
    loginMethod: "email",
    alertDisplay: "none",
    alertMsg: null,
    alertType: "light",
  };
  handleInputChange = (e) => {
    // console.log(e.target.value);
    this.setState({ [e.target.name]: e.target.value });
  };
  formSubmit = (e) => {
    e.preventDefault();
    // var axios = require("axios").default;
    const formData = new FormData();
    formData.append("email", this.state.email);
    formData.append("password", this.state.password);
    formData.append("loginMethod", this.state.loginMethod);
    console.log("loggin....");
    this.props.setLoggedInUser(formData);
  };
  componentDidMount = () => {
    const isLoggedIn = JSON.parse(localStorage.getItem("isLoggedIn"));
    if (isLoggedIn) {
      // console.log(this.props.history.push);
      this.props.history.push("/dashboard");
      console.log("already logged in. redirecting to dashboard");
    }
  };
  render() {
    let { alertType, alertMsg, alertDisplay } = this.props.loginFormAlert;
    return (
      <>
        <main>
          <div className="container my-form">
            <div className="shadow-lg py-5 px-3 mb-5 bg-white rounded text-center">
              <legend>Dashboard Login</legend>
              <div
                className={`alert alert-${alertType} container col-8`}
                role="alert"
                style={{ display: alertDisplay }}
              >
                {alertMsg}
              </div>
              <form
                className="needs-validation"
                autoComplete="off"
                onSubmit={this.formSubmit}
              >
                <div className="col-auto">
                  <label className="sr-only" htmlFor="id_password1">
                    Email
                  </label>
                  <div className="input-group mb-2">
                    <input
                      required
                      style={{ borderRight: "none" }}
                      name="email"
                      type="email"
                      className="form-control"
                      id="email"
                      placeholder="Enter Your Email"
                      value={this.state.email}
                      onChange={this.handleInputChange}
                    />
                  </div>
                </div>
                <div className="col-auto">
                  <label className="sr-only" htmlFor="id_password2">
                    password
                  </label>
                  <div className="input-group mb-2">
                    <input
                      required
                      style={{ borderRight: "none" }}
                      name="password"
                      type="password"
                      className="form-control"
                      id="password"
                      placeholder="Enter Your Password"
                      value={this.state.password}
                      onChange={this.handleInputChange}
                    />
                    <div className="input-group-prepend">
                      <div className="input-group-text">
                        <i
                          data-password-id="password"
                          className="fa fa-eye-slash password-view"
                        />
                      </div>
                    </div>
                  </div>
                </div>
                <button type="submit" className="btn btn-sm btn-primary mt-3">
                  submit
                </button>
              </form>
            </div>
          </div>
        </main>
      </>
    );
  }
}

function mapStateToProps(state) {
  const { isLoggedIn, userData, loginFormAlert } = state.dashboardReducer;
  return { isLoggedIn, userData, loginFormAlert };
}

function mapDispatchToProps(dispatch) {
  return {
    setLoggedInUser: (e) => dispatch(ACTIONS.LoggedInUser(e)),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(Login);
