import React, { Component } from "react";
import { connect } from "react-redux";
// import axios from "axios";
import $ from "jquery";

import SideNav from "./SideNav";
// import API from "../../assets/api.json";
import {
  FetchLiveStream,
  PutLiveStream,
} from "../../store/actions/dashboard_actions";

export class UpdateLiveStream extends Component {
  state = {
    streamId: null,
    title: "",
    url: null,
    thumbnail: null,
    date: null,
    time: null,
    medium: null,
    klass: null,
    discription: null,
    alertType: "light",
    alertMsg: "",
    alertDisplay: "none",
  };
  handleInputChange = (e) => {
    this.setState({ [e.target.name]: e.target.value });
    // console.log(e.target.value);
  };
  handleFileSelection = (e) => {
    this.setState({ [e.target.name]: e.target.files[0] });
    // console.log(e.target.files[0]);
  };
  handleFormSubmit = (e) => {
    e.preventDefault();
    let data = {};
    let { url, thumbnail, date, time, discription } = this.state;
    if (
      url === null &&
      thumbnail === null &&
      date === null &&
      time === null &&
      discription === null
    ) {
      // console.log("get data from reducer", this.props.updateLiveStream);
      data = this.props.updateLiveStream;
    } else {
      // console.log("get data from local state", this.state);
      let url = $("input[name='url']").val();
      let date = $("input[name='date']").val();
      let time = $("input[name='time']").val();
      let discription = $("textarea[name='discription']").val();
      // let thumbnail = $("input[name='thumbnail']")[0].files[0];
      // console.log(url, date, time, discription, thumbnail);
      data = {
        date: date,
        discription: discription,
        time: time,
        title: this.props.updateLiveStream.title,
        url: url,
      };
    }
    this.props.PutLiveStream(this.state.streamId, data);
  };
  componentDidMount = () => {
    const path = window.location.pathname;
    let streamId = parseInt(path.replace("/dashboard/update-stream/", ""));
    this.setState({ streamId: streamId });
    console.log(streamId);
    this.props.FetchLiveStream(streamId);
    console.log("fired from update comp");
  };
  render() {
    const { url, discription, date, time } = this.props.updateLiveStream;
    let {
      alertType,
      alertMsg,
      alertDisplay,
    } = this.props.updateLiveStreamAlert;
    return (
      <main className="pb-5 main col-10 container">
        <div className="row ">
          <div className="col-md-8">
            <div className="container-lg">
              <div className="shadow-lg py-5 px-3 mb-5 bg-white rounded">
                <div className="container my-form">
                  <legend className="text-center">Update LiveStream</legend>

                  <div
                    className={`alert alert-${alertType} container col-8 text-center`}
                    role="alert"
                    style={{ display: alertDisplay }}
                  >
                    {alertMsg}
                  </div>
                  <form
                    onSubmit={this.handleFormSubmit}
                    className="yt-stream-update-form"
                  >
                    <div className="form-group">
                      <label htmlFor="exampleFormControlInput10">
                        Youtube Link
                      </label>
                      <input
                        onChange={this.handleInputChange}
                        name="url"
                        type="url"
                        className="form-control"
                        id="exampleFormControlInput10"
                        placeholder="https://yoube.be/anbsjdjbsk/"
                        defaultValue={url}
                      />
                    </div>
                    <div className="form-group">
                      <label htmlFor="exampleFormControlFile1">Thumbnail</label>
                      <input
                        onChange={this.handleFileSelection}
                        name="thumbnail"
                        type="file"
                        className="form-control-file"
                        id="exampleFormControlFile1"
                      />
                    </div>
                    <div className="form-group">
                      <label htmlFor="exampleFormControlInput11">Date</label>
                      <input
                        name="date"
                        onChange={this.handleInputChange}
                        type="date"
                        className="form-control"
                        id="exampleFormControlInput11"
                        defaultValue={date}
                        required
                      />
                    </div>
                    <div className="form-group">
                      <label htmlFor="exampleFormControlInput12">Time</label>
                      <input
                        name="time"
                        onChange={this.handleInputChange}
                        type="time"
                        className="form-control"
                        id="exampleFormControlInput12"
                        defaultValue={time}
                        required
                      />
                      <small className="text-muted">Fill like "08:40 am"</small>
                    </div>
                    <div className="form-group">
                      <label htmlFor="exampleFormControlSelect1">Medium</label>
                      <select
                        className="form-control"
                        id="exampleFormControlSelect1"
                        name="medium"
                        onChange={this.handleInputChange}
                      >
                        <option>--select--</option>
                        <option>Hindi</option>
                        <option>English</option>
                      </select>
                    </div>
                    <div className="form-group">
                      <label htmlFor="exampleFormControlSelect11">Class</label>
                      <select
                        className="form-control"
                        id="exampleFormControlSelect11"
                        name="class"
                        onChange={this.handleInputChange}
                      >
                        <option>--select--</option>
                        <option value="8th">8th</option>
                        <option value="9th">9th</option>
                        <option value="10th">10th</option>
                      </select>
                    </div>
                    <div className="form-group">
                      <label htmlFor="exampleFormControlSelect12">
                        Subject
                      </label>
                      <select
                        className="form-control"
                        id="exampleFormControlSelect12"
                        onChange={this.handleInputChange}
                      >
                        <option>--select--</option>
                        <option>Math</option>
                        <option>Science</option>
                      </select>
                    </div>
                    <div className="form-group">
                      <label htmlFor="exampleFormControlTextarea1">
                        Description
                      </label>
                      <textarea
                        onChange={this.handleInputChange}
                        name="discription"
                        className="form-control"
                        id="exampleFormControlTextarea1"
                        placeholder="This is my old description."
                        rows={2}
                        defaultValue={discription}
                      />
                    </div>
                    <div className="text-center">
                      <button
                        type="submit"
                        className="btn btn-sm btn-primary mt-3"
                      >
                        submit
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
          <SideNav />
        </div>
      </main>
    );
  }
}

function mapStateToProps(state) {
  let {
    isLoggedIn,
    dataLoaded,
    updateLiveStream,
    updateLiveStreamAlert,
  } = state.dashboardReducer;
  return { isLoggedIn, dataLoaded, updateLiveStream, updateLiveStreamAlert };
}

function mapDispatchToProps(dispatch) {
  return {
    FetchLiveStream: (e) => dispatch(FetchLiveStream(e)),
    PutLiveStream: (streamId, data) => dispatch(PutLiveStream(streamId, data)),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(UpdateLiveStream);
