import * as ACTION_TYPE from "./action_types";
import axios from "axios";

import API from "../../assets/api.json";

export const LoggedInUser = (data) => {
  let options = {
    method: "POST",
    url: API.proxy + "/login",
    headers: {
      "content-type": "multipart/form-data",
    },
    data: data,
  };
  return (dispatch) => {
    axios(options)
      .then((response) => {
        console.log(response);
        const res = response.data;
        console.log(res);
        dispatch({
          type: ACTION_TYPE.LoggedInUser,
          payload: {
            status: true,
            userData: res.Result,
            token: res.Result.access,
            alertType: "light",
            alertMsg: null,
            alertDisplay: "none",
          },
        });
        localStorage.setItem("isLoggedIn", true);
        localStorage.setItem("token", res.Result.access);
        localStorage.setItem("userData", JSON.stringify(res.Result));
        localStorage.setItem("otm", res.Comments);
        // console.log("logged In . now redirect");
        window.location = "/dashboard";
      })
      .catch((error) => {
        console.log("error");
        console.log(error.request);
        let error_msg = "connection failed";
        try {
          error_msg = error.response.data.Comments;
          dispatch({
            type: ACTION_TYPE.LoggedInUser,
            payload: {
              status: true,
              userData: null,
              alertType: "danger",
              alertMsg: error_msg,
              alertDisplay: "block",
            },
          });
        } catch {
          error_msg = "connection failed";
        }
        console.error(error_msg);
      });
  };
};
