import * as ACTION_TYPES from "../actions/action_types";

let initalState = {
  Questions: [],
};

const mocktestReducer = (state = initalState, action) => {
  let payload = action.payload;
  switch (action.type) {
    case ACTION_TYPES.AddQuestionToMockTest:
      let questions = [...state.Questions];
      questions.push(payload);
      return {
        ...state,
        Questions: questions,
      };

    default:
      return {
        ...state,
      };
  }
};

export default mocktestReducer;
