import React, { Component } from "react";
import { connect } from "react-redux";
import { Switch, Route, BrowserRouter as Router } from "react-router-dom";

import { SetUserLoggedIn } from "./store/actions/dashboard_actions";
import Footer from "./components/Footer";
import Navbar from "./components/Navbar";
import LandingContainer from "./containers/Landing";
import PasswordChangeContainer from "./containers/PasswordReset";
import Dashboard from "./containers/Dashboard";
import Login from "./containers/Login";

export class App extends Component {
  componentDidMount = () => {
    const isLoggedIn = JSON.parse(localStorage.getItem("isLoggedIn"));
    const userData = JSON.parse(localStorage.getItem("userData"));
    // console.log(isLoggedIn, userData);
    if (isLoggedIn) {
      this.props.setLoggedInUser(userData);
    }
  };
  render() {
    return (
      <div className="App">
        <Router>
          <Navbar />
          <Switch>
            <Route path="/" exact component={LandingContainer} />
            <Route path="/dashboard" component={Dashboard} />
            <Route path="/login" component={Login} />
            <Route path="/password-reset" component={PasswordChangeContainer} />
          </Switch>
        </Router>
        <Footer />
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {};
}

function mapDispatchToProps(dispatch) {
  return {
    setLoggedInUser: (e) => dispatch(SetUserLoggedIn(e)),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(App);
