import * as ACTION_TYPE from "../actions/action_types";
import testimonialsData from "../../assets/testimonials.json";

let initalState = {
  Testimonials: testimonialsData.testimonials,
};

const landingReducer = (state = initalState, action) => {
  switch (action.type) {
    case ACTION_TYPE.SetTestimonial:
      return {
        ...state,
      };

    default:
      return {
        ...state,
      };
  }
};

export default landingReducer;
