import React, { Component } from "react";
import SideNav from "../SideNav";
import AddQuestionModal from "./AddQuestionModal";
import { connect } from "react-redux";
import { PostMockTest } from "../../../store/actions/mocktest_actions";

export class MockTest extends Component {
  state = {
    name: null,
    time: null,
    instruction: null,
    subject: null,
    class: null,
    board: null,
    pMark: null,
    nMark: null,
    // questions: null,
  };
  handleAddQuestionFormSubmit = (e) => {
    e.preventDefault();
    let {
      name,
      time,
      instruction,
      subject,
      std_class,
      board,
      pMark,
      nMark,
    } = this.state;
    let questions = this.props.Questions;
    // this.setState({ questions: questions });
    let data = {
      name,
      time,
      instruction,
      subject,
      std_class,
      board,
      pMark,
      nMark,
      questions,
    };
    this.props.PostMockTest(data);
  };
  handleInputChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };
  render() {
    // console.log(this.props.Questions);
    return (
      <main className="pb-5 main col-10 container">
        <div className="row ">
          <div className="col-md-8">
            <div className="container-lg">
              <div className="shadow-lg py-5 px-3 mb-5 bg-white rounded">
                <div className="container my-form">
                  <legend className="text-center">Create New MockTest</legend>
                  <div
                    className="alert alert-light container col-8 text-center"
                    role="alert"
                    style={{ display: "none" }}
                  />
                  <form onSubmit={this.handleAddQuestionFormSubmit}>
                    <div className="form-group">
                      <label htmlFor="mt-name">Name</label>
                      <input
                        name="name"
                        type="text"
                        className="form-control"
                        id="mt-name"
                        placeholder="Enter Test Name"
                        onChange={this.handleInputChange}
                      />
                    </div>
                    <div className="form-group">
                      <label htmlFor="mt-time">Time</label>
                      <input
                        name="time"
                        type="text"
                        className="form-control"
                        id="mt-time"
                        placeholder="Enter Test Time"
                        onChange={this.handleInputChange}
                      />
                    </div>

                    <div className="form-group">
                      <label htmlFor="mt-instruction">Instructions</label>
                      <textarea
                        name="instruction"
                        className="form-control"
                        id="mt-instruction"
                        placeholder="This is my old description."
                        rows={2}
                        defaultValue={""}
                        onChange={this.handleInputChange}
                      />
                    </div>
                    <div className="form-group">
                      <label htmlFor="mt-subject">Subject</label>
                      <select
                        className="form-control"
                        id="mt-subject"
                        name="subject"
                        onChange={this.handleInputChange}
                      >
                        <option>--select--</option>
                        <option>Hindi</option>
                        <option>English</option>
                      </select>
                    </div>
                    <div className="form-group">
                      <label htmlFor="mt-class">class</label>
                      <select
                        className="form-control"
                        id="mt-class"
                        name="class"
                        onChange={this.handleInputChange}
                      >
                        <option>--select--</option>
                        <option>8th</option>
                        <option>9th</option>
                        <option>10th</option>
                      </select>
                    </div>
                    <div className="form-group">
                      <label htmlFor="mt-board">board</label>
                      <select
                        className="form-control"
                        id="mt-board"
                        name="board"
                        onChange={this.handleInputChange}
                      >
                        <option>--select--</option>
                        <option>CBSE</option>
                        <option>Other Board</option>
                      </select>
                    </div>
                    <div className="form-group">
                      <label htmlFor="mt-positive-mark">positive-mark</label>
                      <input
                        name="pMark"
                        type="number"
                        className="form-control"
                        id="mt-positive-mark"
                        placeholder="Enter Test positive-mark"
                        onChange={this.handleInputChange}
                      />
                    </div>
                    <div className="form-group">
                      <label htmlFor="mt-negative-mark">negative-mark</label>
                      <input
                        name="nMark"
                        type="number"
                        className="form-control"
                        id="mt-negative-mark"
                        placeholder="Enter Test negative-mark"
                        onChange={this.handleInputChange}
                      />
                    </div>
                    <div className="card">
                      <div className="card-body">
                        <div className="col-12 container">
                          Question
                          <button
                            type="button"
                            className="btn btn-outline-success float-right"
                            data-toggle="modal"
                            data-target="#QuestionModal"
                          >
                            <i className="fa fa-plus"></i>
                          </button>
                          {/* <!-- Modal --> */}
                          <AddQuestionModal />
                          <div className="questions table-responsive mt-3">
                            <table
                              className="table table-hover"
                              style={{
                                textAlign: "center",
                                alignItems: "center",
                              }}
                            >
                              <tbody>
                                {this.props.Questions.map((qn, index) => (
                                  <tr key={index}>
                                    <td>{qn.text}</td>
                                    <td className="table-responsive">
                                      <a
                                        className="btn btn-outline-warning "
                                        href="#/"
                                      >
                                        <i className="fa fa-pencil-square"></i>
                                      </a>
                                    </td>
                                    <td>
                                      <a
                                        className="btn btn-outline-danger"
                                        href="#/"
                                      >
                                        <i className="fa fa-trash"></i>
                                      </a>
                                    </td>
                                  </tr>
                                ))}
                              </tbody>
                            </table>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="text-center">
                      <button
                        type="submit"
                        className="btn btn-sm btn-primary mt-3"
                      >
                        submit
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>

          <SideNav />
        </div>
      </main>
    );
  }
}

function mapStateToProps(state) {
  let { Questions } = state.mocktestReducer;
  return { Questions };
}

function mapDispatchToProps(dispatch) {
  return {
    PostMockTest: (e) => dispatch(PostMockTest(e)),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(MockTest);
