import React, { Component } from "react";
import { Link } from "react-router-dom";
import API from "../assets/api.json";

export class Navbar extends Component {
  state = {
    userImage: null,
  };
  componentDidMount = () => {
    let isLoggedIn = JSON.parse(localStorage.getItem("isLoggedIn"));
    if (isLoggedIn) {
      let image = JSON.parse(localStorage.getItem("userData")).image;
      image = API.proxy + image;
      this.setState({ userImage: image });
    } else {
      this.setState({
        userImage:
          "https://pbs.twimg.com/profile_images/467502291415617536/SP8_ylk9_200x200.png",
      });
    }
  };
  render() {
    return (
      <div className="nav-menu fixed-top">
        <div className="container">
          <div className="row">
            <div className="col-md-12">
              <nav className="navbar navbar-light navbar-expand-lg">
                <a className="navbar-brand" href="/#">
                  <img
                    src="https://mirrorscore.com/static/images/logo.png"
                    className="img-fluid"
                    id="logomain"
                    alt="logo"
                  />
                </a>
                <a className="navbar-brand" href="/#">
                  <img
                    src="https://mirrorscore.com/static/images/logo.png"
                    className="img-fluid"
                    id="logomobile"
                    alt="logo"
                  />
                </a>
                <button
                  className="navbar-toggler"
                  id="mobilemenu"
                  type="button"
                  data-toggle="collapse"
                  data-target="#navbar"
                  aria-controls="navbar"
                  aria-expanded="false"
                  aria-label="Toggle navigation"
                >
                  {" "}
                  <span className="navbar-toggler-icon" />{" "}
                </button>
                <a
                  href="https://play.google.com/store/apps/details?id=com.app.mirrorscore"
                  className="btn button-mob"
                >
                  {" "}
                  Download Now{" "}
                </a>
                <div className="collapse navbar-collapse" id="navbar">
                  <ul className="navbar-nav ml-auto">
                    <li className="nav-item">
                      <a className="nav-link" id="nav-item2" href="/#features">
                        Features
                      </a>
                    </li>
                    <li className="nav-item">
                      <Link className="nav-link" id="nav-item2" to="/dashboard">
                        DashBoard
                      </Link>
                    </li>
                    <li className="nav-item">
                      <a
                        href="https://play.google.com/store/apps/details?id=com.app.mirrorscore"
                        id="nav-item6"
                        className="btn btn-outline-light my-3 my-sm-0 ml-lg-3"
                      >
                        Download Now
                      </a>
                    </li>
                    <li className="nav-item">
                      <a
                        href="#/"
                        className="btn btn-outline-light my-3 my-sm-0 ml-lg-3"
                      >
                        Sign Out
                      </a>
                    </li>
                    <li className="nav-item ml-lg-3">
                      <a href="/dashboard">
                        <img
                          src={this.state.userImage}
                          alt="..."
                          className="rounded-circle"
                          width={40}
                          height={40}
                        />
                      </a>
                    </li>
                  </ul>
                </div>
              </nav>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Navbar;
