import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

import SideNav from "./SideNav";
// import API from "../../assets/api.json";
// import * as ACTIONS from "../../store/actions/actions";

export class ProfileView extends Component {
  state = {
    isLoggedIn: false,
    userData: [],
    studentClass: [],
    studentBoard: []
  };
  componentDidMount = () => {
    let isLoggedIn = JSON.parse(localStorage.getItem("isLoggedIn"));
    if (isLoggedIn) {
      const userData = JSON.parse(localStorage.getItem("userData"));
      const {studentClass, studentBoard} = userData
      this.setState({ userData: userData });
      this.setState({studentClass: studentClass})
      this.setState({studentBoard: studentBoard})
    }
  };
  render() {
    const { userData, studentClass, studentBoard } = this.state; 
    // console.log(userData);
    return (
      <main className="pb-5 main col-10 container">
        <div>
          <div className="row ">
            <div className="col-md-8">
              <div className="container-lg">
                <div className="shadow-lg py-5 px-3 mb-5 bg-white rounded">
                  <div className="row">
                    <div className="col-sm-2">
                      <div className="text-center">
                        <img
                          src={userData.image}
                          alt="profile"
                          className="rounded-circle"
                          width={100}
                          height={100}
                        />
                      </div>
                    </div>
                    <div className="col-sm-10">
                      <div>
                        <div className="mb-4">
                          <span className="text-name mr-2">
                            {userData.firstName + " " + userData.lastName}
                          </span>
                          <span className="more-btn">
                            <span
                              id="dropdownMenuButton"
                              data-toggle="dropdown"
                              aria-haspopup="true"
                              aria-expanded="false"
                            >
                              <svg
                                fill="currentColor"
                                preserveAspectRatio="xMidYMid meet"
                                height={40}
                                width={40}
                                viewBox="0 0 40 40"
                              >
                                <g fill="none" fillRule="evenodd">
                                  <rect
                                    fill="#d8d8d8"
                                    height={40}
                                    opacity=".2"
                                    rx={20}
                                    width={40}
                                  ></rect>
                                  <g fill="#000" fillRule="nonzero">
                                    <circle
                                      cx="20.015625"
                                      cy="13.000016"
                                      r="2.000016"
                                    />
                                    <circle
                                      cx="20.015625"
                                      cy={20}
                                      r="2.000016"
                                    />
                                    <circle
                                      cx="20.015625"
                                      cy="26.999984"
                                      r="2.000016"
                                    />
                                  </g>
                                </g>
                              </svg>
                            </span>
                            <div
                              className="dropdown-menu"
                              aria-labelledby="dropdownMenuButton"
                            >
                              <Link
                                className="dropdown-item text-muted"
                                to="/dashboard/profile-update"
                              >
                                Edit Profile
                              </Link>

                              <Link
                                className="dropdown-item text-muted"
                                to="/dashboard/password-change"
                              >
                                Change Password
                              </Link>
                            </div>
                          </span>
                          <div>
                            <a href={"mailto:" + userData.email}>
                              {userData.email}
                            </a>{" "}
                            |
                            <a href={"tel:+" + userData.phoneNo}>
                              {userData.phoneNo}
                            </a>
                          </div>
                        </div>
                        <div>
                          <p className="text-muted">
                            Class {studentClass.map(sc => (
                              <span>{" "}{sc}{" "}</span>
                            ))}
                          </p>
                        </div>
                        <div>
                          <p className="text-muted">
                            Board {studentBoard.map(sb => (
                              <span>{" "}{sb}{" "}</span>
                            ))}
                          </p>
                        </div>
                        <div>
                          <p className="text-muted" />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <SideNav />
          </div>
        </div>
      </main>
    );
  }
}

function mapStateToProps(state) {
  return {
    Data: state.userReducer,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    // setLoggedInUser: (e) => dispatch(ACTIONS.SetLoggedInUser(e)),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(ProfileView);
